import {
	mapState,
	mapActions
} from 'vuex'
import commongood from '../../components/common/commongood.vue'
export default {
	name: "",
	components: {
		commongood
	},
	props: {},
	data() {
		return {
			// openId
			openId: '',
		}
	},
	methods: {
		...mapActions(['getCollected']),
	},
	mounted() {
		if (uni.getStorageSync('openId')) {
			this.openId = uni.getStorageSync('openId')
		}
		if (this.openId !== '') {
			this.getCollected(this.openId)
		}
	},
	onShow() {
		if (this.openId !== '') {
			this.getCollected(this.openId)
		}
	},
	onLoad() {

	},
	filters: {

	},
	computed: {
		...mapState(['collectArr']),
	},
	watch: {

	},
	directives: {

	}
}
